* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  cursor: not-allowed;
  display: block;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: #212121;
  text-decoration: none;
}

a:hover {
  color: #000;
  font-weight: 700;
}

button {
  width: 70px;
  height: 35px;
  background-color: #d8d5d5;
  border: rgba(0, 0, 0, 0);
  border-radius: 10%;
  outline: rgba(0, 0, 0, 0);
  margin-left: 15px;
}

button:not(:disabled):hover {
  color: #fafafa;
  cursor: pointer;
  background-color: #414141;
  transform: scale(1.2);
}

input {
  margin-left: 10px;
}

input:hover {
  transform: scale(1.1);
}

.container {
  padding: 15px;
}

.timer {
  gap: 10px;
  margin-top: 10px;
  display: flex;
}

.form {
  align-items: center;
  gap: 15px;
  display: flex;
}

.input-wrap {
  flex-direction: column;
  gap: 15px;
  display: flex;
}

